import { CommonService } from '@/services/api/common.service';
import { PropertyService } from '@/services/api/property.service';
import { Component, Host, Prop, State, Watch, h } from '@stencil/core';
import { Locale } from 'date-fns';
import { ICurrency, IExposedLanguages } from '@/models/common';
import axios from 'axios';
import { IExposedProperty } from '@/models/property';
import booking_store, { getVisibleInventory, reserveRooms } from '@/stores/booking';
import app_store from '@/stores/app.store';
import { formatAmount, generateColorShades, getDateDifference } from '@/utils/utils';
// import { roomtypes } from '@/data';

@Component({
  tag: 'ir-testcmp',
  styleUrl: 'ir-testcmp.css',
  shadow: true,
})
export class IrTestcmp {
  @Prop() token: string;
  @Prop() propertyId: number;
  @Prop() baseUrl: string;
  @State() selectedLocale: Locale;
  @State() property: IExposedProperty;
  @State() currencies: ICurrency[];
  @State() languages: IExposedLanguages[];
  private commonService = new CommonService();
  private propertyService = new PropertyService();
  componentWillLoad() {
    axios.defaults.baseURL = this.baseUrl;
    if (this.token) {
      this.initializeApp();
    }
  }
  @Watch('token')
  handleTokenChange() {
    this.initializeApp();
  }
  initializeApp() {
    this.commonService.setToken(this.token);
    this.propertyService.setToken(this.token);
    app_store.app_data = {
      token: this.token,
      property_id: this.propertyId,
    };
    this.initRequest();
  }
  async initRequest() {
    const [property, currencies, languages] = await Promise.all([
      this.propertyService.getExposedProperty({ id: this.propertyId, language: 'en' }),
      this.commonService.getCurrencies(),
      this.commonService.getExposedLanguages(),
    ]);
    this.property = property;
    this.currencies = currencies;
    this.languages = languages;
    // booking_store.roomTypes = [...roomtypes];
    if (this.property.space_theme) {
      const root = document.documentElement;
      const shades = generateColorShades(this.property.space_theme.button_bg_color);
      let shade_number = 900;
      shades.forEach((shade: any, index) => {
        root.style.setProperty(`--brand-${shade_number}`, `${shade.h}, ${shade.s}%, ${shade.l}%`);
        if (index === 9) {
          shade_number = 25;
        } else if (index === 8) {
          shade_number = 50;
        } else {
          shade_number = shade_number - 100;
        }
      });
    }
  }
  renderInternetMessage(isFree: boolean) {
    return isFree ? 'Free Internet' : 'Paid Internet';
  }
  renderRoomTypeAmenities() {
    const checkAminity = (code: string) => {
      return this.property?.amenities.find(a => a.code === code);
    };
    const wifi = checkAminity('freewifi');
    const climatecontrol = checkAminity('climatecontrol');
    const balcony = checkAminity('balcony');
    return (
      <ul class="flex items-center text-xs text-green-500 gap-2 flex-wrap">
        {wifi && (
          <li class="flex items-center gap-2">
            <ir-icons name="wifi" svgClassName="size-4"></ir-icons> <span>{wifi.description}</span>
          </li>
        )}
        {climatecontrol && (
          <li class="flex items-center gap-2">
            <ir-icons name="snowflake" svgClassName="size-4"></ir-icons> <span>{climatecontrol.description}</span>
          </li>
        )}
        {balcony && (
          <li class="flex items-center gap-2">
            <ir-icons name="sun" svgClassName="size-4"></ir-icons> <span>{balcony.description}</span>
          </li>
        )}
      </ul>
    );
  }
  render() {
    if (!this.property) {
      return null;
    }
    return (
      <Host>
        <div class="relative space-y-5 px-4 w-full">
          <section class="w-full z-50 sticky top-0">
            <ir-nav
              exposed_property={this.property}
              website={this.property?.space_theme.website}
              logo={this.property?.space_theme?.logo}
              currencies={this.currencies}
              languages={this.languages}
            ></ir-nav>
          </section>
          <div>
            <ir-property-gallery exposed_property={this.property}></ir-property-gallery>
          </div>
          <div>
            <ir-availibility-header></ir-availibility-header>
          </div>

          <section class="relative rounded-md gap-4 justify-between ">
            <div class="divide-y flex-1 py-2">
              {booking_store.roomTypes?.map(roomType => {
                if (!roomType.is_active || roomType.images.length <= 0 || (roomType.inventory <= 0 && booking_store.enableBooking)) {
                  return null;
                }
                return (
                  <div class="flex flex-col  justify-start gap-4 mb-4   md:flex-row">
                    <div class="hidden md:block">
                      <ir-property-gallery property_state="carousel" roomType={roomType} exposed_property={this.property}></ir-property-gallery>
                    </div>
                    <div class="space-y-1 w-full flex-1 py-2">
                      <h3 class="text-slate-900  font-medium mb-2 text-lg ">{roomType.name}</h3>
                      {/* Mobile view for carousel */}
                      <div class="md:hidden">
                        <ir-property-gallery property_state="carousel" roomType={roomType} exposed_property={this.property}></ir-property-gallery>
                      </div>

                      {booking_store.enableBooking ? (
                        roomType.rateplans.map(ratePlan => {
                          if (!ratePlan.is_active) {
                            return null;
                          }

                          if (!ratePlan.variations) {
                            return null;
                          }
                          const visibleInventory = getVisibleInventory(roomType.id, ratePlan.id);
                          console.log(ratePlan.variations);
                          return (
                            <div key={ratePlan.id} class="bg-gray-50 w-full p-2 gap-2 flex flex-col lg:flex-row space-y-1 text-sm rounded-[var(--radius,8px)]">
                              <div class="flex  justify-between md:justify-start w-full md:w-fit ">
                                <div class="flex items-center gap-2 flex-wrap flex-1 overflow-hidden">
                                  <p>{ratePlan.short_name}</p>
                                  <p class="hidden md:block line-clamp-3 text-slate-700 text-xs">{ratePlan.custom_text}</p>
                                </div>
                                {visibleInventory.reserved > 0 && (
                                  <div class="flex items-start gap-1 md:hidden">
                                    <p class="font-medium">{formatAmount(visibleInventory.reserved * ratePlan?.variations[0].amount, app_store.userPreferences.currency_id)}</p>
                                    <p class="font-medium line-through text-red-500">
                                      {formatAmount(visibleInventory.reserved * ratePlan?.variations[0].total_before_discount, app_store.userPreferences.currency_id)}
                                    </p>
                                  </div>
                                )}
                              </div>
                              <p class="md:hidden line-clamp-3 text-slate-700 text-xs">
                                {ratePlan.custom_text} Lorem ipsum dolor sit amet consectetur adipisicing elit. Itaque at necessitatibus facere voluptas architecto dolorem optio
                                tempore. Itaque delectus quasi dolores doloremque illo provident odio exercitationem quisquam excepturi! Magnam, nemo.
                              </p>
                              {/* <div class="space-y-2 flex items-center">
                                <div class="hidden md:flex items-center gap-4">
                                  <p>Cancelation conditions</p>
                                  <ir-tooltip class="text-gray-700 flex items-center justify-center" message={ratePlan.cancelation}></ir-tooltip>
                                </div>
                              </div> */}
                              <div class="w-full space-y-2  flex flex-col md:space-y-0  md:space-x-2 md:flex-row md:items-center lg:justify-end lg:max-w-md">
                                <ir-select
                                  class="w-full md:w-fit"
                                  label="Travelers"
                                  data={ratePlan.variations.map(v => ({
                                    id: v.amount,
                                    value: `${v.adult_nbr} adults - ${v.child_nbr} children`,
                                  }))}
                                ></ir-select>
                                {visibleInventory.reserved > 0 && (
                                  <div class="hidden md:flex items-center gap-1 flex-col">
                                    <p class="font-medium">{formatAmount(visibleInventory.reserved * ratePlan?.variations[0].amount, app_store.userPreferences.currency_id)}</p>
                                    <p class="font-medium line-through text-red-500">
                                      {formatAmount(visibleInventory.reserved * ratePlan?.variations[0].total_before_discount, app_store.userPreferences.currency_id)}
                                    </p>
                                  </div>
                                )}
                                <ir-select
                                  onValueChange={e => reserveRooms(roomType.id, ratePlan.id, Number(e.detail))}
                                  label="Rooms"
                                  class="w-full md:w-fit"
                                  data={[...new Array(visibleInventory.visibleInventory)]?.map((_, i) => ({
                                    id: i,
                                    value: `${i} ${i === 0 ? '' : formatAmount(ratePlan?.variations[0].amount * i * 10, app_store.userPreferences.currency_id)}`,
                                  }))}
                                ></ir-select>
                              </div>
                            </div>
                          );
                        })
                      ) : (
                        <div class="bg-gray-50 w-full p-2 flex-1  flex flex-col md:flex-row justify-between space-y-1 text-sm rounded-[var(--radius,8px)]">
                          <p>{roomType.description}</p>
                        </div>
                      )}
                    </div>
                  </div>
                );
              })}
            </div>
          </section>
          <section class={'text-sm'}>
            <h2 class="text-lg font-medium mb-5">Facilities and services</h2>
            <ir-facilities properties={this.property}></ir-facilities>
            <p innerHTML={this.property?.description.location_and_intro} class="py-2"></p>
          </section>
        </div>
        <ir-footer exposedProperty={this.property}></ir-footer>
        {booking_store.enableBooking && (
          <div class="z-40 rounded-md rounded-b-none sticky bottom-0 font-medium text-gray-200 gap-10  flex w-full items-center justify-end  bg-gray-700/80">
            <div class="">
              <p>{getDateDifference(booking_store.bookingAvailabilityParams.from_date ?? new Date(), booking_store.bookingAvailabilityParams.to_date ?? new Date())} nights</p>
            </div>
            <ir-button label="Book" class="w-40" buttonStyles={{ height: '40px', borderRadius: '0', borderTopRightRadius: '6px' }}></ir-button>
          </div>
        )}
      </Host>
    );
  }
}
